<template>
    <div class="container">
        <div style="width: 15%;margin-top: 2%;margin-left: 2%">
            <el-upload
                    class="upload-demo"
                    drag
                    :headers="headers"
                    name="UploadForm[file]"
                    :action="importFileUrl"
                    :data="param"
                    :onError="uploadError"
                    :onSuccess="uploadSuccess"
                    :beforeUpload="beforeAvatarUpload"
                    multiple>
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                <div style="font-size: 20px" class="el-upload__tip" slot="tip">只能上传xls/xlsx文件</div>
            </el-upload>
        </div>
      <a href="http://api.wms.com/wms-uploads/download?file-name=qinyang"></a>
      <el-button @click="down" style="width: 15%;margin-top: 2%;margin-left: 2%" type="primary">下载模板<i class="el-icon-download el-icon--right"></i></el-button>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                param: {
                    purpose: 'purpose',
                },
                importFileUrl: 'http://api.wms.com/wms-uploads/upload',
              headers : {
                'Authorization': localStorage.getItem("Authorization"),
                // 'Content-Type':'multipart/form-data;charset=UTF-8'
              }
            }
        },

        methods: {
            // 上传成功后的回调
            uploadSuccess(response, file, fileList) {
                console.log('上传文件', response)
                if(response == 'success'){
                    this.$notify({
                        title: '成功',
                        message: '上传成功',
                        type: 'success'
                    });
                }else{
                    this.$notify({
                      title: '失败',
                      message: '上传失败，请重试',
                      type: 'error'
                    });
                }

            },
            // 上传错误
            uploadError(response, file, fileList) {
                console.log('上传失败，请重试！' + response)
            },
            // 上传前对文件的大小的判断
            beforeAvatarUpload(file) {
                const extension = file.name.split('.')[1] === 'xls'
                const extension2 = file.name.split('.')[1] === 'xlsx'
                const isLt2M = file.size / 1024 / 1024 < 10
                if (!extension && !extension2) {
                    this.$alert('上传模板只能是 xls、xlsx 格式!', '文件错误', {
                        dangerouslyUseHTMLString: true
                    });
                    console.log('上传模板只能是 xls、xlsx 格式!');
                    return false
                }
                if (!isLt2M) {
                    this.$alert('上传模板大小不能超过 10MB!', '大小错误', {
                        dangerouslyUseHTMLString: true
                    });
                    console.log('上传模板大小不能超过 10MB!');
                    return false
                }
                return extension || extension2 && isLt2M
            },
            //下载模板
            down(response) {
              console.log('1');
              // this.$router.push('http://api.wms.com/wms-uploads/download?file-name=qinyang');

              this.$axios({
                  method: 'get',
                  url: '/wms-uploads/download?fileName=qinyang',
                  responseType: 'blob'
              }).then(response => {
                  this.download(response)
              }).catch((error) => {

              })
            },
            download (res) {
                console.log(res)
                //这里res.data是返回的blob对象
                var blob = new Blob([res.data], {type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8'}); //application/vnd.openxmlformats-officedocument.spreadsheetml.sheet这里表示xlsx类型
                var downloadElement = document.createElement('a');
                var href = window.URL.createObjectURL(blob); //创建下载的链接
                downloadElement.href = href;
                downloadElement.download = 'qinyang.xlsx'; //下载后文件名
                document.body.appendChild(downloadElement);
                downloadElement.click(); //点击下载
                document.body.removeChild(downloadElement); //下载完成移除元素
                window.URL.revokeObjectURL(href); //释放掉blob对象
            }
        }
    }
</script>



